import { useState, useEffect, useRef, useCallback } from 'react';
// @mui
import {
    Box,
    Card,
    Stack,
    TextField,
    IconButton,
    Typography,
    Container,
    Paper,
    Avatar,
    Chip,
    CircularProgress,
} from '@mui/material';
// components
import Page from '../../components/Page';
import Iconify from '../../components/Iconify';
import axios from '../../utils/axios';

// ----------------------------------------------------------------------

export default function SupportChat() {
    const [messages, setMessages] = useState([]);
    const [inputText, setInputText] = useState('');
    const [loading, setLoading] = useState(false);
    const [currentHandler, setCurrentHandler] = useState('ai'); // 'ai' or 'agent'
    const [activeTicketId, setActiveTicketId] = useState(null);
    const scrollRef = useRef(null);

    const fetchMessages = useCallback(async (ticketId) => {
        try {
            const response = await axios.get(`/api/support/chat/${ticketId}/messages/user`);
            if (response.data.status === 'success') {
                setMessages(response.data.messages);
                setCurrentHandler(response.data.current_handler);
            }
        } catch (error) {
            console.error('Fetch messages error:', error);
        }
    }, []);

    const fetchActiveTicket = useCallback(async () => {
        try {
            const response = await axios.get('/api/support/tickets');
            if (response.data.status === 'success') {
                const active = response.data.tickets.find((t) => t.status !== 'closed');
                if (active) {
                    setActiveTicketId(active.id);
                    fetchMessages(active.id);
                }
            }
        } catch (error) {
            console.error('Fetch tickets error:', error);
        }
    }, [fetchMessages]);

    useEffect(() => {
        fetchActiveTicket();
    }, [fetchActiveTicket]);

    // Polling for agent replies if handler is agent
    useEffect(() => {
        let interval;
        if (activeTicketId && currentHandler === 'agent') {
            interval = setInterval(() => fetchMessages(activeTicketId), 5000);
        }
        return () => clearInterval(interval);
    }, [activeTicketId, currentHandler, fetchMessages]);

    useEffect(() => {
        if (scrollRef.current) {
            scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
        }
    }, [messages]);

    const handleSendMessage = async () => {
        if (!inputText.trim()) return;

        const userMessage = {
            message: inputText,
            sender_type: 'user',
            created_at: new Date().toISOString(),
        };

        setMessages((prev) => [...prev, userMessage]);
        const text = inputText;
        setInputText('');
        setLoading(true);

        try {
            if (currentHandler === 'agent') {
                // Send to agent endpoint
                await axios.post(`/api/support/chat/${activeTicketId}/send/user`, { message: text });
            } else {
                // Send to AI endpoint
                const response = await axios.post('/api/chat/aboki', { message: text });
                if (response.data.status === 'success') {
                    setActiveTicketId(response.data.conversation_id);
                    if (response.data.message) {
                        setMessages((prev) => [
                            ...prev,
                            {
                                message: response.data.message,
                                sender_type: 'bot',
                                created_at: new Date().toISOString(),
                            },
                        ]);
                    }
                    if (response.data.handler === 'agent') {
                        setCurrentHandler('agent');
                    }
                }
            }
        } catch (error) {
            console.error('Send message error:', error);
        } finally {
            setLoading(false);
        }
    };

    return (
        <Page title="Support Chat">
            <Container maxWidth="md">
                <Typography variant="h4" sx={{ mb: 3 }}>
                    Customer Support
                </Typography>

                <Card sx={{ height: '70vh', display: 'flex', flexDirection: 'column' }}>
                    {/* Header */}
                    <Box sx={{ p: 2, borderBottom: '1px solid #eee', display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                        <Stack direction="row" spacing={2} alignItems="center">
                            <Avatar sx={{ bgcolor: currentHandler === 'ai' ? 'primary.main' : 'success.main' }}>
                                <Iconify icon={currentHandler === 'ai' ? 'eva:robot-outline' : 'eva:person-outline'} />
                            </Avatar>
                            <Box>
                                <Typography variant="subtitle1">
                                    {currentHandler === 'ai' ? 'Aboki AI' : 'Live Support Agent'}
                                </Typography>
                                <Typography variant="caption" color="text.secondary">
                                    {currentHandler === 'ai' ? 'Automated Assistant' : 'Connected to Support Team'}
                                </Typography>
                            </Box>
                        </Stack>
                        {currentHandler === 'ai' && (
                            <Chip label="24/7 Support" color="primary" variant="outlined" size="small" />
                        )}
                    </Box>

                    {/* Messages area */}
                    <Box
                        ref={scrollRef}
                        sx={{
                            flexGrow: 1,
                            p: 3,
                            overflow: 'auto',
                            bgcolor: '#F4F6F8',
                            display: 'flex',
                            flexDirection: 'column',
                            gap: 2,
                        }}
                    >
                        {messages.length === 0 && (
                            <Box sx={{ textAlign: 'center', mt: 5 }}>
                                <Typography variant="body1" color="text.secondary">
                                    Hi! ðŸ‘‹ I'm Aboki. How can I help you today?
                                </Typography>
                                <Typography variant="caption" color="text.disabled">
                                    You can ask about transactions, KYC, or talk to an agent.
                                </Typography>
                            </Box>
                        )}

                        {messages.map((msg, index) => {
                            const isUser = msg.sender_type === 'user';
                            const isBot = msg.sender_type === 'bot';
                            return (
                                <Box
                                    key={index}
                                    sx={{
                                        alignSelf: isUser ? 'flex-end' : 'flex-start',
                                        maxWidth: '80%',
                                    }}
                                >
                                    <Paper
                                        sx={{
                                            p: 2,
                                            bgcolor: isUser ? 'primary.main' : 'white',
                                            color: isUser ? 'white' : 'text.primary',
                                            borderRadius: 2,
                                            boxShadow: 1,
                                        }}
                                    >
                                        <Typography variant="body2">{msg.message}</Typography>
                                        <Typography
                                            variant="caption"
                                            sx={{ display: 'block', mt: 0.5, opacity: 0.7, textAlign: 'right' }}
                                        >
                                            {new Date(msg.created_at).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                                        </Typography>
                                    </Paper>
                                </Box>
                            );
                        })}
                        {loading && currentHandler === 'ai' && (
                            <Box sx={{ alignSelf: 'flex-start' }}>
                                <CircularProgress size={20} thickness={5} />
                            </Box>
                        )}
                    </Box>

                    {/* Footer Input */}
                    <Box sx={{ p: 2, borderTop: '1px solid #eee' }}>
                        <Stack direction="row" spacing={2}>
                            <TextField
                                fullWidth
                                placeholder="Type your message..."
                                value={inputText}
                                onChange={(e) => setInputText(e.target.value)}
                                onKeyPress={(e) => {
                                    if (e.key === 'Enter') handleSendMessage();
                                }}
                                disabled={loading && currentHandler === 'ai'}
                            />
                            <IconButton
                                color="primary"
                                onClick={handleSendMessage}
                                disabled={!inputText.trim() || (loading && currentHandler === 'ai')}
                                sx={{ bgcolor: 'primary.main', color: 'white', '&:hover': { bgcolor: 'primary.dark' } }}
                            >
                                <Iconify icon="eva:paper-plane-fill" />
                            </IconButton>
                        </Stack>
                    </Box>
                </Card>
            </Container>
        </Page>
    );
}
